/* src/App.css */
.App {
  text-align: center;
  background: linear-gradient(135deg, #e3f2fd 30%, #ffffff 100%);
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
}

.MuiPaper-root {
  border-radius: 15px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
}

.MuiTypography-h4 {
  font-weight: 700;
  color: #01579b;
}

.MuiTypography-h6 {
  font-weight: 600;
  color: #0288d1;
}

.MuiTypography-body1 {
  color: #424242;
}

.MuiButton-root {
  border-radius: 20px;
  padding: 12px 24px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s, transform 0.3s;
}

.MuiButton-root:hover {
  background-color: #01579b;
  transform: scale(1.05);
}

.MuiCircularProgress-root {
  color: #0288d1;
}
